/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #000000;
  }
  
  /* Name Title */
  .name {
    color: #00e7ff;
    text-align: center;
    font-size: 2rem;
    margin: 20px 0;
  }
  
  /* Container */
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }
  
  .container img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    margin-top: 20px;
  }
  
  /* Crypto Table Wrapper */
  .crypto-table {
    border: 2px solid #00e7ff;
    border-radius: 8px;
    overflow: hidden;
    width: 90%; /* Adjusted width for responsiveness */
    max-width: 800px; /* Limit the table width on large screens */
    margin: 20px auto;
    background-color: #001f1f;
    color: #ffffff;
  }
  
  /* Table */
  table {
    width: 80%;
    border-collapse: collapse;
  }
  
  thead {
    background-color: #003838;
  }
  
  th {
    text-align: left;
    padding: 12px;
    font-size: 16px;
  }
  
  tbody tr {
    border-bottom: 1px solid #003838;
  }
  
  tbody tr:last-child {
    border-bottom: none;
  }
  
  td {
    padding: 12px;
    font-size: 14px;
    vertical-align: middle;
    color: #ffffff;
  }
  
  /* Coin Icon */
  .coin-icon {
    width: 24px;
    height: 24px;
    margin-right: 6px;
    vertical-align: middle;
  }
  
  /* Background Color for Table Headers */
  .bg-color {
    background-color: #092e33;
    color: #ffffff;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .name {
      font-size: 1.5rem;
    }
  
    th,
    td {
      font-size: 13px;
      padding: 8px;
    }
  
    .coin-icon {
      width: 20px;
      height: 20px;
    }
  }
  
  @media (max-width: 480px) {
    .name {
      font-size: 1.2rem;
    }
  
    .crypto-table {
      width: 95%; /* Use most of the screen width */
    }
  
    th,
    td {
      font-size: 12px;
      padding: 6px;
    }
  
    .coin-icon {
      width: 18px;
      height: 18px;
    }
  } 
  