/* Responsive table styling */
table {
    width: 100%;
    border-collapse: collapse;
  }
  
  tr {
    border-bottom: 1px solid #ccc;
  }
  
  td {
    padding: 10px;
    text-align: left;
    font-size: 16px;
  }
  
  /* Alignment for the content in the first cell */
  .alignment {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  /* Currency icon styling */
  .coin-icon {
    width: 32px;
    height: 32px;
    object-fit: cover;
    border-radius: 50%;
  }
  
  /* Responsive design for smaller screens */
  @media (max-width: 768px) {
    td {
      font-size: 14px;
    }
  
    .coin-icon {
      width: 24px;
      height: 24px;
    }
  }
  
  @media (max-width: 480px) {
    table {
      display: block;
      overflow-x: auto;
    }
  
    td {
      font-size: 12px;
    }
  
    .coin-icon {
      width: 20px;
      height: 20px;
    }
  
    .alignment p {
      font-size: 12px;
    }
  }
  