/* General Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #000000;
  }
  
  /* Loader Container */
  .loader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  /* Title */
  .name {
    color: #00e7ff;
    text-align: center;
    font-size: 2rem;
    margin: 20px 0;
  }
  
  /* Container */
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }
  
  /* Responsive Image */
  .container img {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
  }
  
  /* Table */
  .crypto-table {
    width: 100%;
    max-width: 1200px;
    margin: 20px auto;
    overflow-x: auto;
  }
  
  .crypto-table table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .crypto-table th,
  .crypto-table td {
    padding: 15px;
    text-align: center;
    color: #ffffff;
    border: 1px solid #444;
  }
  
  .bg-color {
    background-color: #333333;
  }
  
  .crypto-table th {
    font-size: 1.2rem;
    font-weight: bold;
  }
  
  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .name {
      font-size: 1.5rem;
    }
  
    .crypto-table th,
    .crypto-table td {
      padding: 10px;
      font-size: 0.9rem;
    }
  }
  
  @media (max-width: 480px) {
    .name {
      font-size: 1.2rem;
    }
  
    .crypto-table th,
    .crypto-table td {
      font-size: 0.8rem;
    }
  }
  